<template>
  <div class="">
    <Header />
    <Nav />

    <swiper class="bannerSwiper" :options="bannerSwiperOption">
      <!--
      <swiper-slide :style="`background:url(${require('@/assets/imgs/banner01.jpg')}) center center no-repeat;background-size:cover;`"></swiper-slide>
      <swiper-slide :style="`background:url(${require('@/assets/imgs/banner02.jpg')}) center center no-repeat;background-size:cover;`"></swiper-slide>
      -->
      <swiper-slide><img src="@/assets/imgs/banner01.jpg" /></swiper-slide>
      <swiper-slide><img src="@/assets/imgs/banner02.jpg" /></swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>

    <div class="intro">
      We specialize in surgical instrument set for 15 years, with great quality and convincing price, we also have our own professional R&D team to meet any of your requirements.
      <h3 class="section-title" style="margin-top:.3rem;"><strong>Main Products</strong></h3>
    </div>

    <div v-for="(level1Item, level1Index) in products" :key="level1Index">
      <h1 class="category1">
        <span v-if="hasLangCN && level1Item.cn_name">{{ level1Item.cn_name }} </span>
        <span v-if="hasLangTW && level1Item.tw_name">{{ level1Item.tw_name }} </span>
        <span v-if="level1Item.en_name">{{ level1Item.en_name }}</span>
      </h1>
      <div class="category2" v-for="(level2Item, level2Index) in level1Item.child" :key="level2Index">
        <div class="product">
          <div class="info">
            <div class="name">
              <h2 v-if="hasLangCN && level2Item.cn_name">{{ level2Item.cn_name }}</h2>
              <h2 v-if="hasLangTW && level2Item.tw_name">{{ level2Item.tw_name }}</h2>
              <h3 v-if="level2Item.en_name">{{ level2Item.en_name }}</h3>
            </div>
          </div>
          <div class="img">
            <template v-if="level2Item.imgs.length > 1">
              <swiper :options="level2SwiperOption">
                <swiper-slide v-for="(level2ImgItem, level2ImgIndex) in level2Item.imgs" :key="level2ImgIndex" :level1Index="level1Index" :level2Index="level2Index">
                  <img :src="level2ImgItem.url" />
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
              </swiper>
              <img :ref="'$SwiperPreviewImg' + level1Index + level2Index" v-for="(level2ImgItem, level2ImgIndex) in level2Item.imgs" :key="level2ImgIndex" :src="level2ImgItem.url" :preview="1" :preview-text="level2Item.en_name" class="previewImg" />
            </template>
            <img v-else v-for="(level2ImgItem, level2ImgIndex) in level2Item.imgs" :key="level2ImgIndex" :src="level2ImgItem.url" :preview="1" :preview-text="level2Item.en_name" />
          </div>
        </div>
        <ul class="productList">
          <li v-for="(level3Item, level3Index) in level2Item.child" :key="level3Index">
            <div class="product">
              <div class="img">
                <template v-if="level3Item.imgs.length > 1">
                  <swiper :options="level3SwiperOption">
                    <swiper-slide v-for="(level3ImgItem, level3ImgIndex) in level3Item.imgs" :key="level3ImgIndex" :level1Index="level1Index" :level2Index="level2Index" :level3Index="level3Index">
                      <img :src="level3ImgItem.url" />
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination"></div>
                  </swiper>
                  <img :ref="'$SwiperPreviewImg' + level1Index + level2Index + level3Index" v-for="(level3ImgItem, level3ImgIndex) in level3Item.imgs" :key="level3ImgIndex" :src="level3ImgItem.url" :preview="1" :preview-text="level3Item.en_name" class="previewImg" />
                </template>
                <img v-else v-for="(level3ImgItem, level3ImgIndex) in level3Item.imgs" :key="level3ImgIndex" :src="level3ImgItem.url" :preview="1" :preview-text="level3Item.en_name" />
              </div>
              <div class="info">
                <h3 class="name" v-if="hasLangCN && level3Item.cn_name">{{ level3Item.cn_name }}</h3>
                <h3 class="name" v-if="hasLangTW && level3Item.tw_name">{{ level3Item.tw_name }}</h3>
                <h4 class="name" v-if="(hasLangCN || hasLangTW) && level3Item.en_name">{{ level3Item.en_name }}</h4>
                <h3 class="name" v-if="!hasLangCN && !hasLangTW && level3Item.en_name">{{ level3Item.en_name }}</h3>
                <div class="attr"
                  v-for="(level3ItemValue, level3ItemKey, level3ItemIndex) in level3Item" :key="level3ItemKey"
                  v-if="!['cn_name', 'tw_name', 'en_name', 'imgs'].includes(level3ItemKey)"
                >
                  <label>{{ level3ItemKey.charAt(0).toUpperCase() + level3ItemKey.slice(1) }}:&nbsp;</label><var>{{ level3ItemValue }}</var>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>

import products from '@/products/index.js';

let vm = null

export default {
  components: {
    Header: () => import('@/components/Header'),
    Nav: () => import('@/components/Nav')
  },
  data() {
    return {
      products,
      bannerSwiperOption: {
        speed: 800,
        autoplay: {
          disableOnInteraction: false,
          delay: 5000
        },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          paginationType: 'progress'
        },
        on: {
          /*
          tap: function () {
            if (!this.clickedSlide) return false
            vm.swiperPreviewImg('', this.realIndex)
          }
          */
        }
      },
      level2SwiperOption: {
        speed: 800,
        autoplay: {
          disableOnInteraction: false,
          delay: 5000
        },
        loop: true,
        effect: 'cube',
        cubeEffect: {
          slideShadows: false,
          shadow: false,
          //shadowOffset: 100,
          shadowScale: 0.6
        },
        pagination: {
          el: '.swiper-pagination',
          paginationType: 'progress'
        },
        on: {
          tap: function () {
            if (!this.clickedSlide) return false
            vm.swiperPreviewImg('' + this.clickedSlide.attributes['level1Index'].nodeValue + this.clickedSlide.attributes['level2Index'].nodeValue, this.realIndex)
          }
        }
      },
      level3SwiperOption: {
        speed: 800,
        autoplay: {
          disableOnInteraction: false,
          delay: 5000
        },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          paginationType: 'progress'
        },
        on: {
          tap: function () {
            if (!this.clickedSlide) return false
            vm.swiperPreviewImg('' + this.clickedSlide.attributes['level1Index'].nodeValue + this.clickedSlide.attributes['level2Index'].nodeValue + this.clickedSlide.attributes['level3Index'].nodeValue, this.realIndex)
          }
        }
      }
    }
  },
  created() {
    vm = this
  },
  mounted() {
  },
  methods: {
    swiperPreviewImg(refIndex, arrIndex) {
      this.$refs['$SwiperPreviewImg' + refIndex][arrIndex].click()
    }
  },
}
</script>

<style lang="scss" scoped>

  // @media (max-width:750px) {

    .bannerSwiper .swiper-slide {
      img {width:100%;display:block;}
    }

    .intro {padding:.2rem .15rem 0;line-height:1.5;font-size:.15rem;word-break:keep-all;background-image:linear-gradient(#8ecbdd 5%, #2191c1);color:#fff;font-family:'PF';text-align:center;
    }

    .section-title {position:relative;text-align:center;padding-bottom:.2rem;font-family:'DCPText-Bold';line-height:1.5;
      &:before {content:'';position:absolute;left:50%;bottom:.12rem;margin-left:-.8rem;width:1.6rem;height:.02rem;background-image:linear-gradient(90deg, rgba(255, 255, 255, .05), #666, rgba(255, 255, 255, .05));}
      strong {position:relative;color:#333;padding:0 .05rem .05rem;font-size:.18rem;
        /*
        &:before {content:'';position:absolute;right:50%;margin-right:.08rem;bottom:-.04rem;width:.4rem;height:.02rem;background-color:#666;}
        &:after {content:'';position:absolute;left:50%;margin-left:.08rem;bottom:-.04rem;width:.4rem;height:.02rem;background-color:#666;}
        */
      }
    }
    /*
    .section-title {position:relative;text-align:center;padding-bottom:.2rem;font-family:'DCPText-Bold';line-height:1.5;
      &:before {content:'';position:absolute;left:50%;bottom:.12rem;margin-left:-.03rem;width:.06rem;height:.06rem;background-color:#eee;border-radius:100%;}
      strong {position:relative;color:#eee;padding:0 .05rem .05rem;
        &:before {content:'';position:absolute;right:50%;margin-right:.08rem;bottom:-.04rem;width:.4rem;height:.02rem;background-color:#eee;}
        &:after {content:'';position:absolute;left:50%;margin-left:.08rem;bottom:-.04rem;width:.4rem;height:.02rem;background-color:#eee;}
      }
    }
    */

    .category1 {font-size:.18rem;padding:.15rem;background-image:linear-gradient(#2191c1, #fff);color:#333;}
    .category2 {padding:0 .15rem;
      > .product {
        .img {
          .previewImg {display:none;}
          img {display:block;width:100%;border-radius:.08rem;}
        }
        .info {padding:.1rem 0;font-size:.16rem;line-height:1.5;display:flex;
          &:before {content:'';border-left:.05rem solid #2191c1;margin-right:.05rem;}
          p {font-size:.14rem;}
        }
      }

      .productList {padding-bottom:.15rem;
        .product {display:flex;margin-top:.15rem;padding-bottom:.15rem;border-bottom:1px solid #eaeaea;
          .img {flex:0 0 auto;width:1rem;margin-right:.15rem;
            /deep/ .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {margin:0 .02rem;}
            /deep/ .swiper-pagination-bullet {width:.04rem;height:.04rem;}
            .previewImg {display:none;}
            img {display:block;width:1rem;border-radius:.04rem;}
          }
          .info {flex:1 1 auto;font-size:.14rem;line-height:1.5;word-break:break-word;
            h3 {font-size:.14rem;}
            .attr {display:flex;font-size:.12rem;
              label {flex:0 0 auto;}
              var {flex:1 1 auto;}
            }
          }
        }
      }
    }

    /deep/ .swiper-pagination-bullet-active {background-color:#2191c1;}
    /deep/ .swiper-pagination {font-size:0;}
  // }
</style>